const Noticia = (props) => {

     const {description, name, url} = props.info;
      
     

     return(
          <div className="card text-white bg-primary mb-3">
        <a href={url} target="_blank" className=" list-group text-white bg-primary mb-1">
             <h3 className="card-title">{name.text}</h3>
        </a>
        </div>
     );


}

export default Noticia;